<template>
    <div class="site">
        <el-row class="top_li">
            <p>当前线路：</p>
            <el-dropdown class="select_"  @command="changeLine">
                <span class="el-dropdown-link">
                    {{save.line.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item v-for="item in line" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
            <el-button type="primary" style="margin-left: 20px;" @click="addSite">+ 添加站点</el-button>
        </el-row>
        <div class="box">
            <p class="title">站点</p>
            <div class="tableCon">
                <el-table
                    :data="site"
                style="width: 100%">
                    <el-table-column
                        prop="sitename"
                        label="线路名称"
                        width="180">
                    </el-table-column>
                    <el-table-column label="操作">
                        <template slot-scope="scope">
                            <el-button size="mini" type="primary" @click="myEdit(scope.$index, scope.row)">编辑</el-button>
                            <el-button size="mini" type="danger" @click="myDelete(scope.$index, scope.row)">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>

        <div class="alert_bg" v-show="addShow"></div>
        <div class="alert" v-show="addShow">
            <div class="con">
                <p class="title">{{save.title}}</p>
                <div class="c">
                    <p>当前线路：</p>
                    <el-dropdown class="select_"  @command="changeLine">
                        <span class="el-dropdown-link">
                            {{save.line.routename}}<i class="el-icon-arrow-down el-icon--right"></i>
                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item v-for="item in line" :key="item.id" :command="item">{{item.routename}}</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </div>
                <div class="s">
                    <el-input v-model="save.sitename" placeholder="请输入内容"></el-input>
                </div>
                <el-row class="d">
                    <el-button type="primary" size="mini" @click="sub">确定</el-button>
                    <el-button size="mini" @click="close">取消</el-button>
                </el-row>
            </div>
        </div>


    </div>
</template>
<script>
import api from "@/api/index.js";
export default {
    data () {
        return {
            line: [],
            site: [],
            save: {
                title: "新增",
                line: {},
                sitename: '',
                status: '' // add edit
            },
            addShow: false,
        }
    },
    methods: {
        // 删除
        myDelete(index, val) {
            this.$confirm('此操作将永久删除该线路, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                api.deleteLinesite(val.id).then(res => {
                    console.log(res)
                    if(res.data == "1") {
                        this.getLinesite(this.save.line.id)
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    } else {
                        this.$message({
                            type: 'info',
                            message: '删除失败'
                        });  
                    }
                })
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        },
        // 修改
        myEdit(index, val) {
            this.addShow = true;
            this.save.title = "修改";
            this.save.status = "edit";
            this.save.sitename = val.sitename;
            console.log(val)
        },
        // 添加站点
        addSite(id) {
            this.save.title = "新增";
            this.save.status = "add";
            this.addShow = true;
            this.save.sitename = "";
        },
        // 确认
        sub() {
            switch(this.save.status) {
                case "add":
                    this.newLinesite();
                    break;
                case "edit":
                    break;
            }
        },
        // 新增
        newLinesite() {
            api.newLinesite({
                routeid: this.save.line.id,
                sitename: this.save.sitename
            }).then(res => {
                this.addShow = false;
                if(res.data.stats == "success") {
                    this.getLinesite(this.save.line.id)
                    this.$message({
                        type: 'success',
                        message: '新增成功!'
                    });
                } else {
                    this.addShow = false;
                    this.$message({
                        type: 'info',
                        message: '新增失败'
                    });  
                }
            })
        },
        // 改变线路
        changeLine(val) {
            console.log(val)
            this.save.line = val;
            this.getLinesite(val.id);
        },
        // 获取线路
        getSelectAll() {
            api.getSelectAll().then(res => {
                console.log(res);
                this.line = res.data.list;
                this.save.line = res.data.list[0]
            })
        },
        // 获取站点
        getLinesite(routeid) {
            api.getLinesite(routeid).then(res => {
                console.log(res.data.list)
                this.site = res.data.list
            })
        },
        // 取消
        close() {
            this.addShow = false;
        }
    },
    created() {
        this.getSelectAll();
        setTimeout( () => {
            this.getLinesite(this.save.line.id)
        }, 200)
    },
}
</script>
<style lang="stylus" scoped>
.el-dropdown
    color #fff
.top_li
    display flex
    align-items center
    p 
        color #fff
.box
    border-radius 12px
    overflow hidden
    background #fff
    margin-top 12px
    .title 
        margin 20px
    .tableCon
        margin 20px
        border 1px solid #eee
        border-bottom none
.alert_bg 
    background rgba(0,0,0,.5)
    width 100%
    height 100vh
    position fixed 
    top 0
    left 0
    z-index 1
.alert 
    position fixed
    left 50%
    top 50%
    transform translate(-50%, -50%)
    z-index 10
    background #fff
    padding 20px
    border-radius 4px
    .el-dropdown
        color #222
    .title
        padding-bottom 10px
    .c 
        display flex
        padding 6px 0
    .s 
        margin 5px 0
    .d 
        display flex
        justify-content flex-end
        padding-top 10px
</style>



